  display: flex;
  justify-content: flex-start;
  width: 35%;
}

.element--3 {
  display: flex;
  flex-direction: column;
  width: 35%;
}

.arsenal {
  background-color: $arsenal
}
.liverpool {
  background-color: $liverpool;
}

.mancity {
  background-color: $mancity;
}

.manutd {
  background-color: $manutd
}

.chelsea {
